.lzTodayHeader {
    display: flex;
    width: 100%;
    justify-content: space-between;
    margin-bottom:8px;
}

.lzTodayHeader__btn{
    font-size:16px;
    display: flex;
    align-items: center;
}

.lzTodoBody{
    padding:12px;
    padding-right:0;
    width:292px;
    height:240px;
    box-shadow: 0 3px 12px lightgray;
    background-color: #FFF7D1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.lzTodoBody__list{
    height:200px;
    padding-right:12px;
    overflow-y: scroll;
}

.lzTodoBody__list::-webkit-scrollbar{
    width:8px;
}

.lzTodoBody__list::-webkit-scrollbar-thumb{
    background-color: #ffe45f;
}

.lzTodoItem{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom:12px;
}

.lzTodoItem__input{
    font-family: 'GmarketSans';
    font-size:16px;
    outline:none;
    width:200px;
    overflow: hidden;
    background-color: #FFF7D1;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.lzTodoItem__buttonList{
    display: flex;
    gap:4px;
}

.lzTodoItem__btn{
    font-size:16px;
}
.lzTodoAdd {
    display: flex;
    padding-right:12px;
    justify-content: space-between;
}

.lzTodoAdd__input {
    padding-left:4px;
    width:220px;
    border-bottom: 2px solid black;
    outline:none;
    font-family: 'GmarketSans';
}

.lzTodoAdd__btn{
    padding:4px;
    background-color: #ffe562;
    border-radius: 4px;
    font-weight:bold;
    font-family: 'GmarketSans';
}