
.lzBodyBox{
    display: flex;
    flex-direction: column;
    width:1664px;
    font-family: GmarketSans;
}
.lzTitle{
    display: flex;
    width:100%;
    height:72px;
    padding-right:100px;
    justify-content: center;
    align-items: center;
    border-bottom:1px solid #CCCCCC;
}

.lzDate{
    display: flex;
    align-items: center;
    margin-top:-12px;
    font-size: 24px;
}
.lzDate__calendar{
    width:40px;
    height:40px;
    margin-right:12px;
}

.lzStopwatch {
    width:192px;
    height:60px;
    box-shadow: 0 3px 12px lightgray;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 32px;;
}

.lzIconBox{
    display: flex;
    gap:60px;
}

.lzIconBox__icon{
    width:100px;
    height:100px;
    border-radius: 20px;
    box-shadow: 0 3px 12px lightgray;
}

.lzMemo{
    display: flex;
    flex-direction: column;
}

.lzMemo__input{
    padding:12px;
    width:560px;
    height:240px;
    box-shadow: 0 3px 12px lightgray;
    outline:none;
    resize:none;
}

.lzCenter{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-shadow: 0 3px 12px lightgray;
    width:360px;
}

.lzCenter__ul{
    margin:20px;
    height:50%;
    display: flex;
    flex-direction: column;
}

.lzCenter__li{
    white-space: nowrap;
    overflow: hidden;
    text-overflow : ellipsis;
    list-style: circle;
}

.lzCenter__li--title{
    font-weight: bold;
    margin-bottom:12px;
}

.lzCenter__icon{
    width:16px;
    height:16px;
    margin-right:4px;
}

.lzTodoList{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
